<template>
    <div class="Task">
        <div class="header"><span>FGO后台管理系统</span></div>
        <div class="content">
            <div class="aside">
                <el-menu :default-active="activeNavName" class="el-menu-vertical" background-color="#545c64"
                    text-color="#fff" active-text-color="#ffd04b">
                    <el-menu-item @click="jumpUrl('order')">
                        订单管理
                    </el-menu-item>
                    <el-submenu index="1">
                        <template slot="title">
                            <span>业务管理</span>
                        </template>
                        <el-menu-item v-for="(item, i) in navList" :key="i" :index="item.name"
                            @click="jumpUrl(item.name)">
                            {{ item.navItem }}
                        </el-menu-item>
                    </el-submenu>
                </el-menu>
            </div>
            <div class="content-right">
                <router-view />
            </div>
        </div>
    </div>
</template>
  
<script>
export default {
    name: "Task",
    components: {
    },
    created() {
        console.log(this.id)
    },
    watch: {
        activeNavName: {
            handler(newVal) {
                this.jumpUrl(newVal);
            },
            immediate: true,
        },
    },
    data() {
        return {
            id: this.$route.query.id,
            /**
             * const 菜单列表
             */
            navList: [
                { name: "PlayerAccount", navItem: "账号信息" },
                { name: "Signin", navItem: "签到" },
                { name: "PM", navItem: "推图" },
                { name: "PMField", navItem: "推图（修炼场）" },
                { name: "PMScenes", navItem: "推图（幕间物语）" },
                { name: "PMStrengthening", navItem: "推图（强化任务）" },
                { name: "PMHunting", navItem: "推图（狩猎）" },
                { name: "Balls", navItem: "搓丸子" },
                { name: "DogFood", navItem: "捞金狗粮" },
                { name: "QP", navItem: "捞QP" },
                { name: "Material", navItem: "捞材料" },
                { name: "GreenSquare", navItem: "捞绿方块" },
                { name: "Skills", navItem: "从者及从者技能满破" },
                { name: "Slave", navItem: "从者羁绊" },
                { name: "Activity", navItem: "活动毕业" },
                { name: "Purchase", navItem: "购买关卡" },
                { name: "Hunting", navItem: "狩猎任务" },
                { name: "Daily", navItem: "日常" },
            ],


            /**
            * var 当前菜单
            */
            activeNavName: "PlayerAccount",
        };
    },

    methods: {
        /**
         * 菜单选取
         */
        selectNav(index) {
            this.activeNavName = index;
        },

        /**
         * 路由跳转
         */
        jumpUrl(path) {
            this.$router.push(path);
        },

        // 事件绑定回调函数
        /**
         * 1. 更新订单列表
         */

        updateOrderList() {
            console.log(this.orderListParams)
            this.$api.getOrder(this.orderListParams).then((res) => {
                if (res.code === "200") {
                    this.$message.success("成功！");
                    this.tableData = res.data;
                } else {
                    this.$message.error(res.message);
                }
            });
        },
        // 根据订单状态更新订单
        updateOrderListByStatus(status) {
            if (status == "all") {
                this.orderListParams.status = "";
            } else {
                this.orderListParams.status = status;
            }
            this.updateOrderList();
        },

        /**
         * 2. 订单状态筛选
         */

        /**
         * 3. 中途结算
         */
        // 多选
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        handleMidSettle() {
            this.$api.midSettle(this.multipleSelection).then((res) => {
                if (res.code === "200") {
                    this.$message.success("成功！");
                } else {
                    this.$message.error(res.message);
                }
            });
        },
        /**
         * 4. 交单结算
         */
        handleSubmitSettle() {
            this.$api.submitSettle(this.multipleSelection).then((res) => {
                if (res.code === "200") {
                    this.$message.success("成功！");
                } else {
                    this.$message.error(res.message);
                }
            });
        },
        /**
         * 5. 添加订单
         */
        handleOrderAddDialog() {
            this.clearAddOrder();
            this.dialogAddFormVisible = true;
        },
        handleAddOrder() {
            this.$api.addOrder(this.addOrderForm).then((res) => {
                if (res.code === "200") {
                    this.$message.success("成功！");
                    this.clearAddOrder();
                } else {
                    this.$message.error(res.message);
                    this.clearAddOrder();
                }
            });
        },
        clearAddOrder() {
            this.addOrderForm = {
                id: "",
                orderNumber: "",
                playerAccount: "",
                playerEncryptionCode: "",
                playerChannel: "1",
                actionPowerRecovery: "NATURAL",
                teamOnHold: "N",
                boostingContent: "",
                orderAmount: "0",
                playerContcat: "",
            };
        },
        /**
         * 9. 修改订单
         */
        handleOrderModifyDialog(row) {
            this.dialogModifyFormVisible = true;

            this.addOrderForm = {
                id: row.id,
                orderNumber: row.orderNumber,
                playerAccount: row.playerAccount,
                playerEncryptionCode: row.playerEncryptionCode,
                playerChannel: row.playerChannel,
                actionPowerRecovery: row.actionPowerRecovery,
                teamOnHold: row.teamOnHold,
                boostingContent: row.boostingContent,
                orderAmount: row.orderAmount,
                playerContcat: row.playerContcat,
            };
        },
        handleModifyOrder() {
            this.$api.modifyOrder(this.addOrderForm).then((res) => {
                if (res.code === "200") {
                    this.$message.success("成功！");
                    this.clearAddOrder();
                } else {
                    this.$message.error(res.message);
                    this.clearAddOrder();
                }
            });

        },

        /**
         * 6. 搜索订单
         */
        handleSearchOrder() {
            const params = {
                searchValue: this.searchValue,
            };
            this.$api.searchOrder(params).then((res) => {
                if (res.code === "200") {
                    this.$message.success("成功！");
                } else {
                    this.$message.error(res.message);
                }
            });
        },
        /**
         * 7. 更改订单状态
         */
        handleChangeOrderStatus(row) {
            const params = {
                id: row.id,
                status: row.status,
            };
            this.$api.changeOrderStatus(params).then((res) => {
                if (res.code === "200") {
                    this.$message.success("成功！");
                } else {
                    this.$message.error(res.message);
                }
            });

        },
        /**
         * 8. 跳转业务页面
         */
        // 跳转订单项业务页面
        handleJumpBusiness(row) {
            const routeData = this.$router.resolve({
                path: '/task',
                query: {
                    id: row.id
                }
            });
            window.open(routeData.href, '_blank');
        },
        /**
         * 10. 分页获取
         */
        handleSizeChange(val) {
            this.orderListParams.pageSize = val;
            this.updateOrderList();
        },
        handleCurrentChange(val) {
            this.orderListParams.pageNum = val;

            this.updateOrderList();

        },



    },
};
</script>
  
